import React, { Component, Fragment } from 'react';
// import Iframe from 'react-iframe';
import './projects.css';
import memoryGif from '../../mem-cards.gif';
import calendar from '../../calendar.png';
import github from '../../GitHub-Mark.png';

class Projects extends Component {
  render() {

    return (
      <Fragment>
        <div className="d-flex flex-column justify-content-center projectwrapper">
          <div className="content singleProject text-center mx-auto mt-5">
            <p className="headlinebackground">A Customisable Memory Game</p>
            <img className="memoryGif" src={memoryGif} alt=""/>

            <a className="btn btn-primary mt-2 mb-2 mx-5 btn-lg d-block" href="https://memory-game-fb235.firebaseapp.com/" target="_blank" rel="noopener noreferrer">Play Memory Game</a>
            <p>Final project at DCI together created in a team of two.</p>
            <a className="btn btn-info mt-2 mb-2 mx-5 btn-lg" href="https://github.com/Bierwurst/memory-game" target="_blank" rel="noopener noreferrer"><img src={github} alt="link to code on github"/></a>
          </div>

          <div className="content singleProject text-center mx-auto mt-5">
            <p className="headlinebackground text-center">Display basic informations from breweryDB</p>
            <img className="theMug" src="https://image.flaticon.com/icons/svg/931/931898.svg" alt="beermug spinner"/>

            <a className="btn btn-primary mt-2 mb-2 mx-5 btn-lg d-block" href="https://build-vdhkzcxldj.now.sh/" target="_blank" rel="noopener noreferrer">Go To Brewery Api</a>
            <p>Browse the various beers of the US</p>
            <a className="btn btn-info mt-2 mb-2 mx-5 btn-lg" href="https://github.com/Bierwurst/brewerydb-get-details" target="_blank" rel="noopener noreferrer"><img src={github} alt="link to code on github"/></a>
          </div>

          <div className="content singleProject text-center mx-auto mt-5">
            <p className="headlinebackground">A react Christmas Calender</p>
            <img className="memoryGif" src={calendar} alt="calender"/>

            <a className="btn btn-primary mt-2 mb-2 mx-5 btn-lg d-block" href="https://build-baujmmvmzx.now.sh/" target="_blank" rel="noopener noreferrer">Go To Christmas Calendar</a>
            <p>Christmas Calendar</p>
            <a className="btn btn-info mt-2 mb-2 mx-5 btn-lg" href="https://github.com/Bierwurst/calendar" target="_blank" rel="noopener noreferrer"><img src={github} alt="link to code on github"/></a>
          </div>

        </div>

      </Fragment>
    )
  }
}

export default Projects;
