import React, { Component } from 'react';
import logo from '../notlogo.svg';
import { TweenLite } from "gsap/all";

class Default extends Component {
  constructor(props, context) {
    super(props, context);
    this.content = null;
    this.myTween = null;
    this.logo = null;
  }

  componentDidMount(){
    // use the node ref to create the animation
    // this.myTween = TweenLite.to(this.logo, 0.5, { scale: .5, autoAlpha: 0, ease: Elastic.easeOut, y: 50  }, "+=0.5")
    this.myTween = TweenLite.to(this.logo, 3, {scale: .7, y: -50, rotationZ: 360});
    
  }

  render() {
    return (
			<div ref={ div => this.content = div } className="col-md-8 m-mx-auto m-my-5 col-sm-10 mx-sm-auto my-sm-5 col-10 mx-auto my-5">
        <div className='content'></div>
					<h3 className="text-center">
						The content you are looking for is somewhere else, please try the navigation.
					</h3>
          <img ref={ img => this.logo = img } src={logo} className="App-logo animate rotateOut" alt="logo" />


      </div>

    );
  }
}

export default Default;
